.oak-text-field {

    // width: 100%;
    
    input, textarea {

        --clip-radius: 10px;

        // border-radius: 100px;
        clip-path: polygon(0 var(--clip-radius), 10px 0, 100% 0, 100% calc(100% - var(--clip-radius)), calc(100% - var(--clip-radius)) 100%, 0 100%);
