        // clip-path: polygon(calc(50% + 5px) 0, 2em 1em, 10em 14em);
        // Initialize colors based on theme
        font-family: 'Open Sans', sans-serif;
        width: 100%;
        color: var(--color-body-invert);
        font-size: 1em;
        outline: none;
        border: none;
        background-color: var(--color-body-dim);
        margin-bottom: 20px;
        box-sizing:border-box;
        padding: 10px 16px;
        // border: 2px solid;
        // border-color: var(--color-body-dim);

        &:focus {
            // background-color: var(--color-body-invert-dim);
            // border-color: var(--color-body-invert-dim);
            // color: var(--color-body);
        }

        &.error {
            // border-color: var(--color-failure);
            background-color: var(--color-failure-dim);

            &:focus {
                color: var(--color-body-invert);
            }
        }

        &.disabled {
            background-color: var(--color-body-invert-dim);
            color: var(--color-body-dim);
        }
    }

    textarea {
        // height: 500px;
        overflow-y: auto;
        resize: none;
        max-height: 200px;

        --clip-radius: 10px;

        // border-radius: 100px;
        clip-path: polygon(0 var(--clip-radius), 10px 0, 100% 0, 100% calc(100% - var(--clip-radius)), calc(100% - var(--clip-radius)) 100%, 0 100%);
        // clip-path: polygon(92.32051% 40%, 93.79385% 43.1596%, 94.69616% 46.52704%, 95% 50%, 94.69616% 53.47296%, 93.79385% 56.8404%, 92.32051% 60%, 79.82051% 81.65064%, 77.82089% 84.50639%, 75.35575% 86.97152%, 72.5% 88.97114%, 69.3404% 90.44449%, 65.97296% 91.34679%, 62.5% 91.65064%, 37.5% 91.65064%, 34.02704% 91.34679%, 30.6596% 90.44449%, 27.5% 88.97114%, 24.64425% 86.97152%, 22.17911% 84.50639%, 20.17949% 81.65064%, 7.67949% 60%, 6.20615% 56.8404%, 5.30384% 53.47296%, 5% 50%, 5.30384% 46.52704%, 6.20615% 43.1596%, 7.67949% 40%, 20.17949% 18.34936%, 22.17911% 15.49361%, 24.64425% 13.02848%, 27.5% 11.02886%, 30.6596% 9.55551%, 34.02704% 8.65321%, 37.5% 8.34936%, 62.5% 8.34936%, 65.97296% 8.65321%, 69.3404% 9.55551%, 72.5% 11.02886%, 75.35575% 13.02848%, 77.82089% 15.49361%, 79.82051% 18.34936%);
        // clip-path: polygon(calc(50% + 5px) 0, 2em 1em, 10em 14em);
        // Initialize colors based on theme
    }

    label {
        font-family: "Roboto", "Helvetica", "Arial", sans-serif;
        display: block;
        // bottom: 8px;
        left: 10px;
        position: relative;
        font-size: 0.8em;
        color: var(--color-body-invert-dim);
        text-transform: capitalize;
    }
}